$dark: #483C32;
$light: #fbf3d4;

body {
    font-family: Arial, Helvetica, sans-serif;
}

title {
    display: flex;
    width: 100%;
    margin-right: auto;
    margin-left: 50%;
    transform: translateX(-50%);
}

.select-button {
    margin-bottom: 5px;
    margin-left: 50%;
    transform: translateX(-50%);
}

h1 {
    font-weight: 700;
}
.piece-pool-container {
    width: 415px;
}

#piece-pool {
    display: flex;
    justify-content: left;
    flex-wrap: wrap;
}

.piece-pool-item {
    width: 100px;
    border: solid 1px black;
}